import React from 'react';
import TopBar from '../components/TopBar';
import BottomNav from '../navigation/BottomNav';

export default function AppLayout({ children }) {
  return (
    <div style={{
      display: 'flex',
      flexDirection: 'column',
      height: '100vh',
      overflow: 'hidden',
    }}>
      {/* 固定头部 */}
      <div style={{
        flexShrink: 0,
        position: 'sticky',
        top: 0,
        zIndex: 1000,
      }}>
        <TopBar />
      </div>

      {/* 可滚动内容区 */}
      <div
        style={{
          flex: 1,
          overflowY: 'auto',
          backgroundColor: '#f5f5f5',
        }}
      >
        {children}
      </div>

      {/* 固定底部导航 */}
      <div style={{
        flexShrink: 0,
        position: 'sticky',
        bottom: 0,
        zIndex: 1000,
      }}>
        <BottomNav />
      </div>
    </div>
  );
}
