import React, { useState } from 'react';
import AppLayout from '../layouts/AppLayout';
import PullToRefresh from '../components/PullToRefresh';
import i18n from '../i18n';
import { useTranslation } from 'react-i18next';

export default function Settings() {
  const { t } = useTranslation();
  const [freeCpu, setFreeCpu] = useState(true);

  const handleRefresh = async () => {
    console.log(t('refreshing'));
  };

  const toggleLanguage = () => {
    const newLang = i18n.language === 'zh' ? 'en' : 'zh';
    i18n.changeLanguage(newLang);
  };

  return (
    <AppLayout>
      <PullToRefresh onRefresh={handleRefresh}>
        <div style={{ padding: 16, background: '#F5F6FA', minHeight: '100vh' }}>
          {/* 分组 1 */}
          <Group>
            <Row>
              <RowLeft title={t('freeCPU')} />
              <label className="switch">
                <input
                  type="checkbox"
                  checked={freeCpu}
                  onChange={(e) => setFreeCpu(e.target.checked)}
                />
                <span className="slider round"></span>
              </label>
            </Row>

            <Row pressable onClick={toggleLanguage}>
              <RowLeft title={t('language')} />
              <RowValue
                text={i18n.language === 'zh' ? t('simplifiedChinese') : t('english')}
                showChevron
              />
            </Row>
          </Group>

          {/* 分组 2 */}
          <Group>
            <Row pressable onClick={() => alert(t('recommend'))}>
              <RowLeft title={t('recommend')} />
              <Chevron />
            </Row>

            <Row pressable onClick={() => alert(t('clearCache'))}>
              <RowLeft title={t('clearCache')} />
              <Chevron />
            </Row>
          </Group>
        </div>
      </PullToRefresh>
    </AppLayout>
  );
}

/* ============ 子组件 ============ */
function Group({ children }) {
  return <div style={{ marginBottom: 16 }}>{children}</div>;
}

function Row({ children, pressable = false, onClick }) {
  return (
    <div
      onClick={pressable ? onClick : undefined}
      style={{
        minHeight: 56,
        backgroundColor: '#fff',
        borderRadius: 12,
        marginBottom: 10,
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'space-between',
        padding: '0 16px',
        boxShadow: '0 1px 4px rgba(0,0,0,0.06)',
        cursor: pressable ? 'pointer' : 'default',
      }}
    >
      {children}
    </div>
  );
}

function RowLeft({ title, subtitle }) {
  return (
    <div style={{ display: 'flex', flexDirection: 'column' }}>
      <span style={{ fontSize: 16, color: '#111827', fontWeight: 600 }}>{title}</span>
      {subtitle && (
        <span style={{ fontSize: 12, color: '#6B7280', marginTop: 2 }}>{subtitle}</span>
      )}
    </div>
  );
}

function RowValue({ text, showChevron = false }) {
  return (
    <div style={{ display: 'flex', alignItems: 'center', gap: 6 }}>
      {text && (
        <span
          style={{
            fontSize: 14,
            color: '#6B7280',
            maxWidth: 200,
            overflow: 'hidden',
            textOverflow: 'ellipsis',
            whiteSpace: 'nowrap',
          }}
        >
          {text}
        </span>
      )}
      {showChevron && <Chevron />}
    </div>
  );
}

function Chevron() {
  return <span style={{ fontSize: 20, color: '#C7C9D1' }}>›</span>;
}

/* ============ 样式增强 ============ */
const styleEl = document.createElement('style');
styleEl.innerHTML = `
.switch {
  position: relative;
  display: inline-block;
  width: 46px;
  height: 24px;
}
.switch input { display: none; }
.slider {
  position: absolute;
  cursor: pointer;
  top: 0; left: 0; right: 0; bottom: 0;
  background-color: #E5E7EB;
  transition: .4s;
  border-radius: 34px;
}
.slider:before {
  position: absolute;
  content: "";
  height: 18px;
  width: 18px;
  left: 3px;
  bottom: 3px;
  background-color: white;
  transition: .4s;
  border-radius: 50%;
}
input:checked + .slider {
  background-color: #34C759;
}
input:checked + .slider:before {
  transform: translateX(22px);
}
`;
document.head.appendChild(styleEl);
