import React, { useState } from 'react';
import PullToRefresh from '../components/PullToRefresh';

import { useNavigate } from 'react-router-dom';
import AppLayout from '../layouts/AppLayout';

export default function Community() {
  const navigate = useNavigate();
  const [loading, setLoading] = useState(false);

  const handleRefresh = async () => {
    setLoading(true);
    try {
      // 这里未来可以加上从后端获取社区相关数据的逻辑
      await new Promise(resolve => setTimeout(resolve, 1000));
    } finally {
      setLoading(false);
    }
  };

  return (
        <AppLayout>
    <PullToRefresh onRefresh={handleRefresh}>
      <div
        style={{
          minHeight: '100vh',
          backgroundColor: '#f5f6fa',
          paddingBottom: '70px',
        }}
      >

        <div style={{ padding: '15px' }}>
          {/* 顶部标题 */}
          <h2 style={{ fontSize: 20, marginBottom: 16, color: '#222' }}>
            社区功能
          </h2>

          {/* 栏目卡片 - 流动性 */}
          <div
            onClick={() => navigate('/liquidity')}
            style={{
              backgroundColor: '#fff',
              borderRadius: 12,
              padding: 16,
              marginBottom: 12,
              boxShadow: '0 2px 6px rgba(0,0,0,0.05)',
              cursor: 'pointer',
              transition: 'all 0.2s ease-in-out',
            }}
          >
            <h3 style={{ fontSize: 16, color: '#2196F3', marginBottom: 6 }}>
              💧 流动性
            </h3>
            <p style={{ fontSize: 14, color: '#555', margin: 0 }}>
              查看并参与流动性池，赚取交易手续费奖励。
            </p>
          </div>

          {/* 栏目卡片 - 质押/存款 */}
          <div
            onClick={() => navigate('/staking')}
            style={{
              backgroundColor: '#fff',
              borderRadius: 12,
              padding: 16,
              marginBottom: 12,
              boxShadow: '0 2px 6px rgba(0,0,0,0.05)',
              cursor: 'pointer',
              transition: 'all 0.2s ease-in-out',
            }}
          >
            <h3 style={{ fontSize: 16, color: '#FF9800', marginBottom: 6 }}>
              💎 质押 / 存款
            </h3>
            <p style={{ fontSize: 14, color: '#555', margin: 0 }}>
              存入代币赚取分红或奖励，支持灵活和定期存款。
            </p>
          </div>

          {loading && (
            <div style={{ textAlign: 'center', color: '#888', marginTop: 20 }}>
              正在刷新数据...
            </div>
          )}
        </div>
      </div>
    </PullToRefresh>
    </AppLayout>
  );
}
