// src/components/PageTitle.jsx
import { useEffect } from 'react';
import { useLocation } from 'react-router-dom';

export default function PageTitle() {
  const location = useLocation();

  useEffect(() => {
    switch (location.pathname) {
      case '/':
        document.title = '首页';
        break;
      case '/community':
        document.title = '社区';
        break;
      case '/profile':
        document.title = '我的';
        break;
      default:
        document.title = 'Simple DApp';
    }
  }, [location.pathname]);

  return null; // 这个组件不渲染任何 DOM
}
